@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Suez+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  width: 100%;
  min-height: 100vh;
  background-color: #4B4B4B;
  text-align: center;
  font-family: 'Roboto', sans-serif;
}

.level {
  font-family: 'Suez One', serif;
  color: #EEEEEE;
  text-shadow: 0px 4px 2px #2B2B2B;
}
.header {
  background-color: #eeeeee;
  box-shadow: 0px 4px 1px 0 #a4a4a4;
  display: flex;
  align-items: center;
  width: 100%;
  height: 12vh;
}

.logo {
  font-size: 30px;
  margin-left: 8%;
  color: #4b4b4b;
  font-weight: bold;
}

.y-logo {
  font-weight: 400;
  color: transparent;
  background-color: #a3924d;
  -webkit-background-clip: text;
          background-clip: text;
  font-family: 'Suez One', serif;
  text-shadow: 0px 2px 4px #e8d06f;
}

.score {
  color: #eeeeee;
  text-shadow: 0px 3px 4px #4b4b4b;
  margin-left: auto;
  margin-right: 8%;
  font-weight: bold;
  font-size: 30px;
}

@media (min-width: 1440px) {
  .score,
  .logo,
  .level {
    font-size: 40px;
  }
}

@media (max-width: 768px) {
  .score,
  .logo {
    font-size: 20px;
  }
}

@media (max-width: 425px) {
  .score,
  .logo {
    font-size: 16px;
  }
}

.main-content {
  width: 100%;
}

.cards-box {
  padding-bottom: 20px;
  padding-left: 4%;
  display: grid;
  grid-template-columns: repeat(4, 20vw);
  grid-gap: 20px 4%;
  gap: 20px 4%;
  width: 90%;
}

.level {
  margin: 5vh 0px;
}

@media (max-width: 768px) {
  .main-content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .cards-box {
    grid-template-columns: repeat(2, 40vw);
    width: 80%;
  }
}

@media (max-width: 425px) {
  .cards-box {
    grid-template-columns: 1fr;
    width: 60%;
  }
}
.card {
  cursor: pointer;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #eeeeee;
  width: 20vw;
  height: 60vh;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}

.card::after {
  content: '';
  position: absolute;
  top: 0;
  display: block;
  width: 100%;
  height: 100%;
  opacity: 0;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  box-shadow: 4px 0px 1px 0px #a4a4a4;
  transition: opacity 0.2s ease-in;
}

.card:hover::after {
  opacity: 1;
}

.card-img {
  position: relative;
  width: 80%;
  height: 82%;
  z-index: 1;
}

.card-name {
  position: absolute;
  bottom: 0;
  color: #eeeeee;
  font-size: 24px;
  text-shadow: 0px 3px 4px #4b4b4b;
  text-align: center;
  align-self: center;
  z-index: 1;
}

.vector {
  bottom: 0;
  position: absolute;
  width: 100%;
  height: 245px;
  background: url(/memory_of_the_stone/static/media/vector.7d417fb2.svg);
}

@media (min-width: 1800px) {
  .vector {
    height: 40%;
    background-size: cover;
  }

  .card-name {
    font-size: 40px;
  }
}

@media (max-width: 768px) {
  .card {
    width: 80%;
    height: 50vh;
  }

  .card-name {
    font-size: 22px;
  }
}

@media (max-width: 425px) {
  .card {
    width: 100%;
  }
}

.text-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 88vh;
}

.level-up,
.lose {
  margin: 0;
  font-family: 'Suez One', serif;
  color: #eeeeee;
  text-shadow: 0px 3px 4px #1a1a1a;
  font-weight: 400;
  font-size: 32px;
}

.sm-txt {
  position: relative;
  margin: 0;
  font-size: 24px;
  font-weight: bold;
  color: #e8d06f;
  margin-bottom: 20px;
}

.again {
  cursor: pointer;
  position: relative;
  top: 0;
  background-color: #eeeeee;
  box-shadow: 0px 4px 1px 0px #aaaaaa;
  border-radius: 0px;
  border: none;
  padding: 10px 15px;
}

.again:active {
  top: 4px;
  box-shadow: 0px 0px 1px 0px #aaaaaa;
}

@media (min-width: 1441px) {
  .lose {
    font-size: 40px;
  }

  .sm-txt {
    font-size: 38px;
  }

  .again {
    width: 160px;
    height: 80px;
    font-size: 24px;
    box-shadow: 0px 8px 1px 0px #aaaaaa;
  }

  .again:active {
    top: 8px;
  }
}

.loadingBox {
  display: grid;
  place-items: center;
  width: 100%;
  height: 88vh;
}

.loading {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 20px solid #e8d06f8f;
  border-left: 20px solid transparent;
  border-top: 20px solid transparent;
  animation: rotate 1s linear 0s forwards infinite;
}

@keyframes rotate {
  to {
    transform: rotate(360deg);
  }
}

.footer {
  margin-top: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 50px;
  color: #EEEEEE;
}

.logo-github {
  margin-left: 10px;
  width: 30px;
}
